<template>
  <v-container>
    <v-row>
      <v-col cols="12" md="12" class="d-flex align-center justify-center">
        <v-card class="text-center" flat>
          <div>
            <p class="mb-0 custom__title">Contact</p>
          </div>
          <div>
            <p class="custom__subtitle">
              Just leave me a message if you want to know more,
              or you have project in mind
            </p>
          </div>

          <v-divider color="#0096C7"></v-divider>
          <v-form ref="form" v-model="form" class="mt-5">
            <div class="mb-6">
              <v-text-field
                v-model="email"
                type="email"
                name="email"
                :rules="[rules.email]"
                label="Email Address*"
              ></v-text-field>
            </div>

            <div>
              <v-textarea
                :rules="[rules.length(9)]"
                label="Share your message*"
                v-model="text"
                name="message"
                dense
              >
              </v-textarea>
            </div>
          </v-form>
          <div>
            <v-btn
              color="success"
              rounded
              @click="send()"
              :loading="loading"
              large
              :disabled="!form"
              type="submit"
              value="Send"
              >Send</v-btn
            >
          </div>
        </v-card>
      </v-col>
      <v-col cols="12" md="12">
        <v-row align="end" justify="end">
          <div>
            <v-snackbar v-model="m" v-if="yes">
              {{ m }}
              <v-btn text color="success" @click.native="m = false"
                >Close</v-btn
              >
            </v-snackbar>
            <v-snackbar v-model="m" v-if="no">
              {{ m }}
              <v-btn text color="error" @click.native="m = false">Close</v-btn>
            </v-snackbar>
          </div>
        </v-row>
      </v-col>
    </v-row>
  </v-container>
</template>

<script>
import * as fb from "../firebase";
export default {
  data: () => ({
    email: "",
    text: "",
    loading: false,
    yes: false,
    no: false,
    m: "",
    form: false,
    rules: {
      email: (v) => !!(v || "").match(/@/) || "Please enter a valid email",
      length: (len) => (v) =>
        (v || "").length >= len || `Invalid character length, required ${len}`,
      required: (v) => !!v || "This field is required",
    },
  }),
  methods: {
    send() {
      this.loading = true;
      const data = {
        email: this.email,
        message: this.text,
      };
      fb.messageCollection
        .add(data)
        .then(() => {
          (this.yes = true),
            (this.loading = false),
            (this.m = "Thank you for contacting me");
          console.log("message sent successfully");
          setTimeout(() => {
            
            this.email = '',
            this.text = ''
          }, 1500);
        })
        .catch((err) => {
          (this.m = err),
            (this.loading = false),
            (this.no = true),
            console.log(err);
        });
    },
  },
};
</script>
